import * as React from 'react';
import { DentalDetails, DentalProcedureData, DropdownChoice, TicketData, MemberData } from 'Pages/CustomerCarePage/Models';
import { Grid } from '@material-ui/core';
import { InputArea } from 'Pages/CustomerCarePage/Components/InputArea';
import { Dropdown } from 'Pages/CustomerCarePage/Components/Dropdown';
import { GlobalFunction } from 'Pages/CustomerCarePage/GlobalFunction';
import { API } from 'Pages/CustomerCarePage/API';
import { cloneDeep } from 'lodash';
import { Loading } from 'Pages/CustomerCarePage/Components/Loading';
import { AdjudicationModal } from '../AdjudicationStepModal';
import { IconButton } from 'Pages/CustomerCarePage/Components/IconButton';
import { faInfoCircle } from '@fortawesome/free-solid-svg-icons';

interface IProps {
    ticket: TicketData;
    member: MemberData;
    dental: DentalDetails;
    refreshDentalState: () => void;
}

/* All needed for adjudication step */
interface IStates {
    canCoverCanAvailResults: any[];
    adjudicationState: {
        isModalOpen?: boolean;
        isErrorModalOpen?: boolean;
        member?: any;
        facility?: {
            facility_is_affiliated: boolean;
            doctor_is_affiliated: boolean;
            procedure_is_valid: boolean;
        };
        steps?: any;
        assessment?: string;
        procedure?: any;
    };
    isLoading: boolean;
}

export class ApproveDentalSection extends React.Component<IProps, IStates> {

    assessmentType = [
        { value: '', label: '' },
        { value: 'Approved', label: 'Approved' },
        { value: 'Disapproved', label: 'Disapproved' },
        { value: 'Pending', label: 'Pending' }
    ];

    constructor(props: IProps) {
        super(props);

        this.state = {
            canCoverCanAvailResults: [],
            adjudicationState: {
                facility: {
                    facility_is_affiliated: true,
                    doctor_is_affiliated: true,
                    procedure_is_valid: true
                }
            },
            isLoading: true
        };
    }










    componentDidMount() {

        const contractId = this.props.ticket ? this.props.ticket.contractId || '' : ''
        API.get('customer-care/tickets/' + this.props.ticket.id, (ticketresponse) => {
            console.log(ticketresponse)
            API.get(`customer-care/members/${ticketresponse.member_data._id}/contracts/${contractId}`, (response) => {
                console.log('benefits',response);
                API.get(`/customer-care/member-utilization-loa/contract/${contractId}/member/${response.member.member_id}`, (response2) => {
                        
                    const canCoverCanAvails: any[] = [];

                    this.state.adjudicationState.member = response.member;
                    this.setState({adjudicationState: this.state.adjudicationState});

                    this.props.dental.procedures.forEach(item => {
                        const benefitNodes = cloneDeep(response.benefits);
                        const toothNumbers = item.toothNumbers.replace(/\s/g, '').split(',').map(item => parseInt(item));
                        const res = GlobalFunction.canCoverCanAvail(
                            contractId, 
                            'DUMMY001', 
                            item.cptCode.label, 
                            this.props.ticket.specificType === 'Outpatient Procedure' ? 'Procedures' : 
                            this.props.ticket.specificType === 'Emergency Room' ? 'Emergency' : 
                            this.props.ticket.specificType === 'Consultation' ? 'Consultations' : 
                            this.props.ticket.specificType === 'Dental Procedure' ? 'Dental' : '' , 
                            response2, response.member,
                            benefitNodes,
                            this.props.ticket.totalAmount, 
                            0,
                            toothNumbers);

                        console.log('canCoverCanAvail', 
                            'contractId: ', contractId, '\n', 
                            'diagnosis: DUMMY001\n', 
                            'procedure: ', item.cptCode.label, '\n', 
                            'ticket_type: ', 
                            this.props.ticket.specificType === 'Outpatient Procedure' ? 'Procedures' : 
                            this.props.ticket.specificType === 'Emergency Room' ? 'Emergency' : 
                            this.props.ticket.specificType === 'Consultation' ? 'Consultations' : 
                            this.props.ticket.specificType === 'Dental Procedure' ? 'Dental' : '', '\n',
                            'member_utilization: ', JSON.stringify(response2), '\n', 
                            'member: ', JSON.stringify(response.member), '\n', 
                            'benefits: ', JSON.stringify(benefitNodes), '\n', 
                            'total_amount: ', this.props.ticket.totalAmount, '\n', 
                            'amount: ', 0, '\n', 
                            'tooth_numbers: ', JSON.stringify(toothNumbers), '\n');

                        const status:any[] = [];
                        res.map(resItem => {
                            resItem.forEach(resItem2 => {
                                status.push(resItem2.approved)
                            })
                        });
                        if(response.member.member_id){
                            status.push(true)
                        } else {
                            status.push(false)
                        }
                        if(response.member.first_name &&
                            response.member.registered_name &&
                            response.member.date_of_birth &&
                            response.member.gender){
                            status.push(true)
                        } else {
                            status.push(false)
                        }
                        if(response.member.member_status==='Active'){
                            status.push(true)
                        } else {
                            status.push(false)
                        }
                        if (this.props.ticket.specificType === 'Dental Procedure' && 
                            !res['PROCEDURE_VALIDATION_COVERABLE']) {
                            status.push(false);
                        }

                        if (this.props.ticket.specificType === 'Dental Procedure' && 
                            res['MEMBER_LIMIT_VALIDATION_ONCE_LIFETIME_PROCEDURE'] && res['MEMBER_LIMIT_VALIDATION_ONCE_LIFETIME_PROCEDURE']['assessments'] &&
                            res['MEMBER_LIMIT_VALIDATION_ONCE_LIFETIME_PROCEDURE']['assessments'].length>0) {
                            console.log('here')
                            for (let i=0;i<res['MEMBER_LIMIT_VALIDATION_ONCE_LIFETIME_PROCEDURE']['assessments'].length;i++) {
                                console.log('inloop '+res['MEMBER_LIMIT_VALIDATION_ONCE_LIFETIME_PROCEDURE']['assessments'][i].approved)
                                if (!res['MEMBER_LIMIT_VALIDATION_ONCE_LIFETIME_PROCEDURE']['assessments'][i].approved) {
                                    console.log('inside false')
                                    status.push(false);
                                    break;
                                }
                            }
                        }

                        canCoverCanAvails.push({
                            cptCode: item.cptCode.value,
                            canCoverCanAvailstep: res,
                            status: status.findIndex(item => !item) === -1 ? 'Approved' : 'Disapproved'
                        })

                        console.log('TESTING', canCoverCanAvails);
                    }); 

                    this.setState({
                        canCoverCanAvailResults: canCoverCanAvails,
                        isLoading: false
                    });
                }, () => {
                    this.setState({isLoading: false});
                });
            }, () => {
                this.setState({isLoading: false});
            });
        }, () => {
            this.setState({isLoading: false});
        });
    }










    render(): JSX.Element {
        return (
            this.state.isLoading ? (
                <Loading height='10rem' />
            ) : (
                this.renderMainContent()
            )
        );
    }
    









    renderMainContent(): JSX.Element {
        const dental = this.props.dental;
        const adjudication = this.state.adjudicationState;

        return (
            <div className='section'>
                <div style={{ paddingTop: '2.5rem' }} />

                {adjudication.assessment && adjudication.isModalOpen ? (
                    <AdjudicationModal
                        memberData={adjudication.member}
                        contractId={
                            this.props.ticket ? 
                            this.props.ticket.contractId || '' : 
                            ''}
                        adjudicationFacilities={adjudication.facility}
                        adjudicationSteps={adjudication.steps}
                        systemassess={adjudication.assessment}
                        procedure={adjudication.procedure}
                        dental={this.props.dental}
                        // diagnosis={this.state.diagnosis}
                        clientName={
                            this.props.member && this.props.member.client ? 
                            this.props.member.client.registeredName || '' : 
                            ''}
                        ticketData={this.props.ticket}
                        isModalOpen={adjudication.isModalOpen}
                        onClose={() => {
                            adjudication.isModalOpen = false;
                            this.setState({adjudicationState: adjudication});
                        }}
                        changeTreeId={() => {
                            // this.onChangeProcedure(this.state.diagIndex,this.state.procIndex,event.target.value)
                        }} />
                ) : null}

                <div className='header-1'>Dental Details</div>

                <div className='sub-section'>
                    <Grid container spacing={1}>
                        {this.renderInfoItem(3, 'DENTIST NAME', dental.dentist.label)}
                        {this.renderInfoItem(3, 'DENTAL CLINIC', `${dental.provider.value} - ${dental.provider.label}`)}
                    </Grid>
                    <Grid container className='sub-section' spacing={2}>
                        {dental.procedures.map(procedure => {
                            return this.renderCardItem(procedure)
                        })}
                    </Grid>
                </div>
                
                <div className='section' />
                <div className='section' />
            </div>
        );
    }










    renderCardItem(procedure: DentalProcedureData): JSX.Element {
        const canCoverCanAvailResult = this.state.canCoverCanAvailResults;
        const selectedIndex = canCoverCanAvailResult.findIndex(item => item.cptCode === procedure.cptCode.value);

        return (
            <Grid item xs={6}>
                <div className='availment-item'>
                    <div className='availment-item-header' />
                    <div className='small-section' style={{borderBottom: '1px solid #EEEEEE'}}>
                        <Grid container spacing={1}>
                            {this.renderInfoItem(3, 'CPT Code', procedure.cptCode.value, 'header-2')}
                            {this.renderInfoItem(true, 'Procedure', procedure.cptCode.label, 'header-2')}
                        </Grid>
                    </div>
                    <div className='small-section'>
                        <Grid container spacing={2}>
                            {this.renderInfoItem(3, 'Tooth Number',      procedure.toothNumbers.length ? procedure.toothNumbers : 'N/A')}
                            {this.renderInfoItem(4, 'Date of Availment', GlobalFunction.toCommonDateString(procedure.dateAdded))}
                            {this.renderInfoItem(5, 'Remaining Balance', `PHP ${GlobalFunction.formatNumber(0, true)}*`)}
                            {this.renderInfoItem(3, 'Number of Surfaces', procedure.noOfSurfaces || 'N/A')}
                            {this.renderInfoItem(4, 'System Assessment', (
                                <>
                                {canCoverCanAvailResult.length > 0 ? canCoverCanAvailResult[selectedIndex]['status'] : 'Unavailable'}
                                <IconButton 
                                    icon={faInfoCircle}
                                    onClick={() => {
                                        if (canCoverCanAvailResult.length > 0) {
                                            
                                            if (selectedIndex != -1) {
                                                this.state.adjudicationState.isModalOpen = true;
                                                this.state.adjudicationState.procedure = cloneDeep(procedure);
                                                this.state.adjudicationState.assessment = canCoverCanAvailResult[selectedIndex]['status'];
                                                this.state.adjudicationState.steps = canCoverCanAvailResult[selectedIndex]['canCoverCanAvailstep'];
                                                this.setState({adjudicationState: this.state.adjudicationState});
                                            }
                                        } else {
                                            this.state.adjudicationState.isErrorModalOpen = true;
                                            this.setState({adjudicationState: this.state.adjudicationState});
                                        }
                                    }} />
                                </>
                            ))}
                            {this.renderInfoItem(5, 'Clause', 'Laboratory tests, x-rays, and other diagnostic examinations prescribed by the INTELLICARE physician*')}
                        </Grid>
                    </div>
                    <div className='small-section availment-item-footer'>
                        <Grid container spacing={2}>
                            <Grid item xs={5}>
                                <Dropdown
                                    placeholder='Choose Assessment'
                                    value={{ 
                                        value: procedure.assessmentType, 
                                        label: procedure.assessmentType 
                                    }}
                                    choices={this.assessmentType}
                                    onChange={(value: DropdownChoice) => {
                                        // @ts-ignore
                                        procedure.assessmentType = value.value;
                                        this.props.refreshDentalState();
                                    }} />
                            </Grid>
                            <Grid item xs={7}>
                                <InputArea
                                    placeholder='Remarks'
                                    value={procedure.assessmentRemarks}
                                    onChange={(value: string) => {
                                        procedure.assessmentRemarks = value;
                                        this.props.refreshDentalState();
                                    }} />
                            </Grid>
                        </Grid>
                    </div>
                </div>
            </Grid>
        );
    }










    renderInfoItem(xs: any, label: string, content: React.ReactNode, contentClassName?: string): JSX.Element {
        return (
            xs ? (
                <Grid item xs={xs}>
                    <div className='light-text'>{label}</div>
                    <div className={contentClassName || 'small-text'}>{content}</div>
                </Grid>
            ) : (
                <>
                <div className='light-text'>{label}</div>
                <div className={contentClassName || 'small-text'}>{content}</div>
                </>
            )
        );
    }
}