import * as React from 'react';
import { DropdownChoice, DentalDetails, DentalProcedureData } from 'Pages/CustomerCarePage/Models';
// import { ComplexTable } from 'Pages/CustomerCarePage/Components/ComplexTable';
import { DetailedDropdown } from 'Pages/CustomerCarePage/Components/DetailedDropdown';
import { CustomTable } from 'Pages/CustomerCarePage/Components/CustomTable';
import { TableCell, TableRow } from '@material-ui/core';
import { NumberField } from 'Pages/CustomerCarePage/Components/NumberField';
import { TextButton } from 'Pages/CustomerCarePage/Components/TextButton';
import { faPlus, faPen, faMinusCircle, faCheckCircle, faTimesCircle, faInfoCircle } from '@fortawesome/free-solid-svg-icons';
import { Global } from 'Pages/CustomerCarePage/Global';
import { InputField } from 'Pages/CustomerCarePage/Components/InputField';
import { IconButton } from 'Pages/CustomerCarePage/Components/IconButton';
import { ItemListPopper } from 'Pages/CustomerCarePage/Components/ItemListPopper';
import { API } from 'Pages/CustomerCarePage/API';

interface IProps {
    dental: DentalDetails;
    facilities: DropdownChoice[];
    doctors: DropdownChoice[];
    createTicketAttemp: boolean;
    refreshDentalState: () => void;
    loadDoctors: (facilityCode: string) => void;
    loadFacilities: () => void;
}

interface IStates {
    procedure: DropdownChoice;
    toothNumbers: string;
    noOfSurfaces: number;

    itemToEditIndex: number;
    editProcedure: DropdownChoice;
    editToothNumbers: string;
    editNoOfSurfaces: number;

    isTeethChartOpen: boolean;
    teethChartAnchor: HTMLButtonElement | null;
}

export class DentalSection extends React.PureComponent<IProps, IStates> {

    procedureWithRequirements: string[];

    constructor(props: IProps) {
        super(props);

        this.state = {
            procedure: { value: '', label: '' },
            toothNumbers: '',
            noOfSurfaces: 0,

            itemToEditIndex: -1,
            editProcedure: { value: '', label: '' },
            editToothNumbers: '',
            editNoOfSurfaces: 0,

            isTeethChartOpen: false,
            teethChartAnchor: null
        };

        this.procedureWithRequirements = [];
    }

    componentDidMount() {
        this.loadDentalRequirements();
    }

    loadDentalRequirements = () => {
        API.get('customer-care/tickets/dental-requirements', (response) => {
            this.procedureWithRequirements = response;
        });
    }

    hasCompleteRequirements = (procedure: DropdownChoice, toothNumbers: string, noOfSurfaces: number) => {
        
        if (procedure.label.length === 0)
            return false;

        if (this.procedureWithRequirements.includes(procedure.value))
            if (toothNumbers.length && noOfSurfaces) 
                return true;
            else
                return false;

        return true;
    }

    getFilteredCptCodes = () => {
        if(Global.CPT_CODES){
            return Global.CPT_CODES.filter(cptCodes => {
                return this.props.dental.procedures.find(procedures =>{
                    return procedures.cptCode.value === cptCodes.value
                }) === undefined
            })
        }
        return [];
    }

    pushProcedure = () => {
        this.props.dental.procedures.push({
            cptCode: this.state.procedure,
            isPHIC: false,
            amount: 0,
            notes: '',
            doctor: '',
            dateAdded: new Date(),
            assessmentType: '',
            assessmentRemarks: '',
            approvedAmount: 0,
            loaNo: 0,
            toothNumbers: this.state.toothNumbers,
            noOfSurfaces: this.state.noOfSurfaces
        });
    }
    

    render(): JSX.Element {
        return (
            <div className='availment-section'>
                <div style={{paddingTop: '5rem'}} />

                <ItemListPopper
                    className='teeth-chart'
                    isOpen={this.state.isTeethChartOpen}
                    anchor={this.state.teethChartAnchor}
                    items={[
                        {
                            content: (
                                <img src='teeth_chart.png' />
                            ),
                            onClick: () => {
                                this.setState({isTeethChartOpen: false});
                            }
                        }
                    ]} />

                <div className='sub-section'>
                    <div className='header-2'>AVAILMENT DETAILS</div>
                    <DetailedDropdown 
                        choices={this.props.facilities}
                        placeholder='Name of Clinic/Hospital*'
                        value={this.props.dental.provider}
                        error={
                            this.props.createTicketAttemp && 
                            this.props.dental.provider.label === ''}
                        onFocus={() => {
                            this.props.loadFacilities();
                        }}
                        onChange={(value: DropdownChoice) => {
                            this.props.loadDoctors(value.label);
                            this.props.dental.provider = value;
                            this.props.dental.dentist = { value: '', label: '' };
                            this.props.refreshDentalState();
                        }} />
                    <DetailedDropdown 
                        choices={this.props.doctors}
                        placeholder='Dentist*'
                        value={this.props.dental.dentist}
                        error={
                            this.props.createTicketAttemp && 
                            this.props.dental.dentist.label === ''}
                        onChange={(value: DropdownChoice) => {
                            this.props.dental.dentist = value;
                            this.props.refreshDentalState();
                        }} />
                </div>
                <div className='sub-section'>
                    <div className='header-3'>LIST OF AVAILMENTS</div>
                    <div className='text'>Add a procedure</div>
                    <TextButton 
                        icon={faInfoCircle} 
                        label='Check Teeth Chart'
                        onClick={(event: any) => {
                            this.setState({
                                teethChartAnchor: event.currentTarget,
                                isTeethChartOpen: !this.state.isTeethChartOpen
                            });
                        }} />
                    <div className='sub-section'>
                        <CustomTable
                            rows={this.props.dental.procedures}
                            renderHeader={() => this.renderTableHeader()}
                            renderRow={(rowData: any, index: number) => 
                                this.renderTableRow(rowData, index)} />
                    </div>
                </div>
                <div className='section' />
            </div>
        );
    }

    renderTableHeader(): JSX.Element {
        return (
            <>
            <TableCell style={{minWidth: '200px', maxWidth: '200px'}}>
                <DetailedDropdown 
                    placeholder='Procedure'
                    choices={this.getFilteredCptCodes()}
                    value={this.state.procedure}
                    onFocus={() => {
                        this.loadDentalRequirements();
                    }}
                    onChange={(value: DropdownChoice) => {
                        this.setState({procedure: value});
                    }} />
            </TableCell>
            <TableCell style={{minWidth: '100px', maxWidth: '100px'}}>
                <InputField
                    placeholder='Tooth Number/s'
                    value={this.state.toothNumbers}
                    onChange={(value: string) => {
                        this.setState({toothNumbers: value});
                    }} />
            </TableCell>
            <TableCell style={{minWidth: '100px', maxWidth: '100px'}}>
                <NumberField
                    placeholder='No. of Surfaces'
                    value={this.state.noOfSurfaces}
                    onChange={(value: number) => {
                        this.setState({noOfSurfaces: value});
                    }} />
            </TableCell>
            <TableCell style={{textAlign: 'center'}}>
                {this.hasCompleteRequirements(
                    this.state.procedure, 
                    this.state.toothNumbers, 
                    this.state.noOfSurfaces) ? (
                    <TextButton 
                        icon={faPlus} 
                        label='Add procedure'
                        onClick={() => {
                            this.pushProcedure();
                            this.props.refreshDentalState();
                            this.setState({
                                procedure: { value: '', label: '' },
                                toothNumbers: '',
                                noOfSurfaces: 0,
                            });
                        }} />
                ) : null}
            </TableCell>
            </>
        );
    }

    renderTableRow(procedure: DentalProcedureData, index: number): JSX.Element {
        const backgroundColor = (index % 2 === 0 ? '#FFFFFF' : '#F6F7FA');
        return (
            <TableRow style={{backgroundColor: backgroundColor}}>
                {this.state.itemToEditIndex === index ? (
                    <>
                    <TableCell style={{minWidth: '200px', maxWidth: '200px'}}>
                        <DetailedDropdown 
                            choices={this.getFilteredCptCodes()}
                            value={this.state.editProcedure}
                            onChange={(value: DropdownChoice) => {
                                this.setState({editProcedure: value});
                            }} />
                    </TableCell>
                    <TableCell style={{minWidth: '100px', maxWidth: '100px'}}>
                        <InputField
                            value={this.state.editToothNumbers}
                            onChange={(value: string) => {
                                this.setState({editToothNumbers: value});
                            }} />
                    </TableCell>
                    <TableCell style={{minWidth: '100px', maxWidth: '100px', textAlign: 'center'}}>
                        <NumberField
                            value={this.state.editNoOfSurfaces}
                            onChange={(value: number) => {
                                this.setState({editNoOfSurfaces: value});
                            }} />
                    </TableCell>
                    <TableCell style={{textAlign: 'right'}}>
                        {this.hasCompleteRequirements(
                            this.state.editProcedure, 
                            this.state.editToothNumbers, 
                            this.state.editNoOfSurfaces) ? (
                            <IconButton
                                icon={faCheckCircle}
                                onClick={() => {
                                    this.props.dental.procedures[index].cptCode = this.state.editProcedure;
                                    this.props.dental.procedures[index].toothNumbers = this.state.editToothNumbers;
                                    this.props.dental.procedures[index].noOfSurfaces = this.state.editNoOfSurfaces;
                                    this.props.refreshDentalState();
                                    this.setState({itemToEditIndex: -1});
                                }}/>
                        ) : null}
                        <IconButton
                            icon={faTimesCircle}
                            onClick={() => {
                                this.setState({itemToEditIndex: -1});
                            }}/>
                    </TableCell>
                    </>
                ) : (
                    <>
                    <TableCell style={{minWidth: '200px', maxWidth: '200px'}}>
                        {procedure.cptCode.value + ' - ' + procedure.cptCode.label}
                    </TableCell>
                    <TableCell style={{minWidth: '100px', maxWidth: '100px'}}>
                        {procedure.toothNumbers.length ? procedure.toothNumbers : 'N/A'}
                    </TableCell>
                    <TableCell style={{minWidth: '100px', maxWidth: '100px', textAlign: 'center'}}>
                        {procedure.noOfSurfaces || 'N/A'}
                    </TableCell>
                    <TableCell style={{textAlign: 'right'}}>
                        <IconButton
                            icon={faPen}
                            onClick={() => {
                                this.setState({
                                    itemToEditIndex: index,
                                    editProcedure: procedure.cptCode,
                                    editToothNumbers: procedure.toothNumbers,
                                    editNoOfSurfaces : procedure.noOfSurfaces,
                                });
                            }}/>
                        <IconButton
                            icon={faMinusCircle}
                            onClick={() => {
                                this.props.dental.procedures.splice(index, 1);
                                this.props.refreshDentalState();
                            }}/>
                    </TableCell>
                    </>
                )}
            </TableRow>
        );
    }
}